import React from 'react';
import './product.scss';

class Product extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            data: [
                {
                    id: 1,
                    url: '/img/banner-xpa@2x.png'
                },
                {
                    id: 2,
                    url: '/img/tidewallet@2x.png'
                },
                // {
                //     id: 3,
                //     url: '/img/bobi@2x.png'
                // }
            ]
        };
    }

    renderItem()
    {
        let items = this.state.data.map((data) => {
            return (
                <div className="item" key={data.id} style={{ backgroundImage: `url('${data.url}')` }}>
                    <div className="paddingTop" />
                </div>
            );
        });

        return items;
    }

    render()
    {
        return (
            <div className="product">
                {this.renderItem()}
            </div>
        );
    }
}

Product.defaultProps = {};

Product.propTypes = {};

export default Product;
