import React from 'react';
import update from 'immutability-helper';
import PropTpe from 'prop-types';
import { translate } from 'react-i18next';
import { mailTo } from '../../utils/rpc';
import WithPopup from '../../components/withPopup/withPopup';
import './contact.scss';

@translate('common', { wait: true })
class Contact extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            form: {
                fullName: '',
                mail: '',
                subject: '',
                message: ''
            }
        };

        this.changeForm = this.changeForm.bind(this);
        this.submitForm = this.submitForm.bind(this);
    }

    changeForm(e)
    {
        this.setState(update(this.state, {
            form: {
                [e.target.name]: { $set: e.target.value }
            }
        }));
    }

    async submitForm(func)
    {
        const { fullName, mail, subject, message } = this.state.form;
        const { t } = this.props;

        //  檢查e-mail正規表示法
        const mailValid = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if (!mailValid.test(mail)) return func(t('contact.mailAlert'));
        if (message.length <= 0) return func(t('contact.messageAlert'));

        const res = await mailTo({
            subject,
            content: `sender?${fullName}/mail?${mail}/message?${message}`
        });
        if (res)
        {
            func(t('contact.success'));
            this.setState(update(this.state, {
                form: {
                    $set: {
                        fullName: '',
                        mail: '',
                        subject: '',
                        message: ''
                    }
                }
            }));
        }
        else
        {
            func(t('contact.error'));
        }
    }

    render()
    {
        const { fullName, mail, subject, message } = this.state.form;
        const { t } = this.props;
        return (
            <WithPopup>
                {(modalControl, toastOpen) => (
                    <div className="c_contact">
                        <h2>
                            <span>{t('contact')}</span>
                        </h2>
                        <div className="data">
                            <div className="title">
                                <input type="text" name="fullName" value={fullName} placeholder="Full Name" onChange={this.changeForm} />
                            </div>
                            <div className="address">
                                <input type="text" name="mail" value={mail} placeholder="Mail Address" onChange={this.changeForm} />
                            </div>
                            <div className="name">
                                <input type="text" name="subject" value={subject} placeholder="Subject" onChange={this.changeForm} />
                            </div>
                        </div>
                        <div className="message">
                            <textarea name="message" id="message" value={message} cols="30" rows="10" placeholder="message..." onChange={this.changeForm} />
                        </div>
                        <a className="submit" onClick={() => { this.submitForm(toastOpen); }} >
                            <span>Submit</span>
                        </a>
                    </div>
                )}
            </WithPopup>
        );
    }
}

Contact.defaultProps = {
    t: () => {}
};

Contact.propTypes = {
    t: PropTpe.func
};

export default Contact;
