import React from 'react';
import update from 'immutability-helper';
import PropTypes from 'prop-types';
import { language } from '../../constants/languageType';
import i18n from '../../i18n/i18n';

class Language extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            lang: localStorage.getItem('i18nextLng'),
        };

        this.changeLang = this.changeLang.bind(this);
    }

    changeLang(lan)
    {
        const tar = language.find(l => l.name === lan);
        i18n.changeLanguage(tar.name);
        this.setState(update(this.state, {
            lang: { $set: lan }
        }));
        if (this.props.changeHeader)
        {
            this.props.changeHeader(lan);
        }
    }

    renderLanguage()
    {
        const list = language.map((l) =>
        {
            return (
                <div key={l.name} onClick={() => this.changeLang(l.name)}>{l.type}</div>
            );
        });
        return (
            <div className="lang-list">
                {list}
            </div>
        );
    }

    render()
    {
        const { lang } = this.state;
        // const { t } = this.props;
        let dl = (language.find(l => l.name === lang));
        if (dl)
        {
            dl = dl.type;
        }
        else
        {
            dl = 'English';
        }
        return (
            <div className="language">
                <div>{dl}<img src="/img/icon/ic-collapse.svg" alt="collapse" /></div>
                {this.renderLanguage()}
            </div>
        );
    }
}

Language.defaultProps = {
    changeHeader: () => {}
};

Language.propTypes = {
    changeHeader: PropTypes.func
};

export default Language;
