import React from 'react';
import PropTypes from 'prop-types';
import { formatNumber, bnHex, tenFill } from '../../utils/format';
import './price.scss';

class Price extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            tokens: { ...finalConfig.assetToken, XPA: finalConfig.XPA }
        };
    }

    renderItem()
    {
        if (this.props.tradeList.ETH)
        {
            return this.props.tradeList.ETH.slice(0, 6).map((data) => {
                return (
                    <div className="item" key={data.address}>
                        <div className="title">{data.desc}</div>
                        <div className="price">{formatNumber(9, tenFill(bnHex(data.latest).toString(), -18))}</div>
                        <div className={`magnitude ${data.growth < 0 ? 'down' : 'up'}`}>{data.growth >= 0 ? `+${data.growth}` : data.growth}%</div>
                    </div>
                );
            });
        }
        else
        {
            return Object.keys(this.state.tokens).map((key) =>
            {
                return (
                    <div className="item" key={key}>
                        <div className="title">{key}/ETH</div>
                        <div className="price">{0}</div>
                        <div className="magnitude up">+0%</div>
                    </div>
                );
            });
        }
    }

    render()
    {
        return (
            <div className="price">
                {this.renderItem()}
            </div>
        );
    }
}

Price.defaultProps = {};

Price.propTypes = {
    tradeList: PropTypes.object.isRequired,
};

export default Price;
