import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { translate } from 'react-i18next';

import connectFirebase from '../../hoc/connectFirebase';
import * as bookActions from '../../actions/book';
import * as userActions from '../../actions/user';
import * as modalActions from '../../actions/modal';
import * as newsActions from '../../actions/news';
import Header from './header';
import Price from './price';
import Product from './product';
import Feature from './feature';
import Subscribe from './subscribe';
import Contact from './contact';
import './landingPage.scss';

function mapStateToProps(state)
{
    return {
        from: state.book.from,
        to: state.book.to,
        tradeList: state.book.tradeList,
        user: state.user.user
    };
}

function mapDispatchToProps(dispatch)
{
    return {
        actions: {
            bookActions: bindActionCreators(bookActions, dispatch),
            userActions: bindActionCreators(userActions, dispatch),
            modalActions: bindActionCreators(modalActions, dispatch),
            newsActions: bindActionCreators(newsActions, dispatch),
        }
    };
}
@connectFirebase
@translate('landing', { wait: true })
class LandingPage extends React.Component
{
    constructor(props)
    {
        super(props);

        this.state = {
            boxRotate: ['rotate(20deg)', 'rotate(140deg)', 'rotate(0)', 'rotate(270deg)', 'rotate(0)', 'rotate(-20deg)', 'rotate(60deg)', 'rotate(-90deg)', 'rotate(40deg)', 'rotate(120deg)', 'rotate(-30deg)', 'rotate(0deg)', 'rotate(-120deg)', 'rotate(40deg)', 'rotate(-120deg)', 'rotate(-60deg)']
        };

        this.animate = '';
    }

    componentDidMount()
    {
        // 全部的圖片
        let arr = document.querySelectorAll('.box');
        // 初始化角度
        for (let i = 0; i < arr.length; i += 1)
        {
            arr[i].style.transform = `${this.state.boxRotate[i]}`;
        }

        this.animate = () =>
        {
            // 目前移動的距離
            let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;

            // 計算移動的百分比
            let movePercent = (scrollTop / this.field.offsetHeight) * 100;

            for (let i = 0; i < arr.length; i += 1)
            {
                if (i % 2 === 0)
                {
                    arr[i].style.transition = '.2s';
                    arr[i].style.transform = `translateY(${movePercent - 10}%) ${this.state.boxRotate[i]} rotateX(${movePercent - 20}deg)`;
                }
                else
                {
                    arr[i].style.transition = '1s';
                    arr[i].style.transform = `translateY(${movePercent}%) ${this.state.boxRotate[i]} rotateX(${movePercent}deg)`;
                }
                // 修改透明度
                arr[i].style.opacity = 100 - (movePercent * 3);
            }
        };

        document.addEventListener('scroll', this.animate);
    }

    componentWillUnmount()
    {
        document.removeEventListener('scroll', this.animate);
    }

    render()
    {
        const { t } = this.props;
        return (
            <div className="co_landingPage ">

                {/* getStarted */}

                <section className="getStarted" ref={(d) => { this.field = d; }}>
                    <img src="/img/mine4.svg" alt="mine" className="box" />
                    <img src="/img/mine3.svg" alt="mine" className="box" />
                    <img src="/img/mine2.svg" alt="mine" className="box" />
                    <img src="/img/mine1.svg" alt="mine" className="box" />
                    <img src="/img/mine1.svg" alt="mine" className="box" />
                    <img src="/img/mine1.svg" alt="mine" className="box" />
                    <img src="/img/mine2.svg" alt="mine" className="box" />
                    <img src="/img/mine1.svg" alt="mine" className="box" />
                    <img src="/img/mine1.svg" alt="mine" className="box" />
                    <img src="/img/mine1.svg" alt="mine" className="box" />
                    <img src="/img/mine1.svg" alt="mine" className="box" />
                    <img src="/img/mine3.svg" alt="mine" className="box" />
                    <img src="/img/mine1.svg" alt="mine" className="box" />
                    <img src="/img/mine1.svg" alt="mine" className="box" />
                    <img src="/img/mine1.svg" alt="mine" className="box" />
                    <img src="/img/mine1.svg" alt="mine" className="box" />
                    <Header />
                    <div className="container">
                        <div className="title">
                            <h2>{t('slogan')}</h2>
                        </div>
                        <div className="getStarted" >
                            <Link to="exchange">
                                {/* <p>{t('accept')}</p> */}
                                <span>{t('start')}</span>
                            </Link>
                        </div>
                        <div className="getStarted beta">
                            <a href={`https://beta.xpa.exchange/?${Date.now()}/#/exchange`} target="_blank" rel="noopener noreferrer">
                                <p>{t('accept-beta')}</p>
                                <span>{t('start-beta')}</span>
                            </a>
                        </div>
                        <Price tradeList={this.props.tradeList} />
                    </div>
                </section>

                {/* product */}
                <section className="product">
                    <div className="container">
                        <Product />
                    </div>
                </section>

                {/* feature */}
                <section className="feature">
                    <div className="container">
                        <div className="title">Why XPA Exchange?</div>
                        <Feature />
                    </div>
                </section>

                <div className="bg-holder">
                    {/* subcribe */}
                    <section className="subcribe">
                        <div className="container">
                            <Subscribe />
                        </div>
                    </section>

                    {/* contact */}
                    <section className="contact">
                        <div className="container">
                            <Contact />
                        </div>
                    </section>
                    <footer>
                        <p>Powered by</p>
                        <div>
                            <a href="https://www.tideisun.com/" target="_blank" rel="noopener noreferrer">
                                <img src="/img/logo-lite.png" alt="tideisun" />
                            </a>
                        </div>
                        <p>
                            Copyright
                            <span>©</span>
                            TideiSun. All rights reserved.
                        </p>
                        {/* <span>Terms & Privacy Policy</span> */}
                    </footer>
                </div>
            </div>
        );
    }
}

LandingPage.defaultProps = {
    t: () => {},
};

LandingPage.propTypes = {
    t: PropTypes.func,
    tradeList: PropTypes.object.isRequired
};

export default connect(mapStateToProps, mapDispatchToProps)(LandingPage);
