import React from 'react';
import { translate } from 'react-i18next';
import PropTypes from 'prop-types';
import update from 'immutability-helper';
import { Link } from 'react-router-dom';
import Language from '../../components/language/language';
import './header.scss';

@translate('landing', { wait: true })
class Header extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            lang: localStorage.getItem('i18nextLng'),
        };

        this.changeHeader = this.changeHeader.bind(this);
    }

    changeHeader(lan)
    {
        this.setState(update(this.state, {
            lang: { $set: lan }
        }));
    }

    scrollTo()
    {
        window.scroll({
            top: document.querySelector('section.contact').offsetTop,
            behavior: 'smooth'
        });
    }

    render()
    {
        const { t } = this.props;
        const { lang } = this.state;

        const guide = () =>
        {
            switch (lang)
            {
                case 'zh-cn':
                    return 'https://www.weibo.com/ttarticle/p/show?id=2309404226836979686902';
                case 'zh-tw':
                    return 'https://medium.com/@xpa_76300/xpa-exchange-%E6%96%B0%E6%89%8B%E4%B8%8A%E8%B7%AF-e85247bae3b';
                default:
                    return 'https://medium.com/tideisun-blog/xpa-exchange-a-gif-guide-to-making-a-trade-4eda6f7c3c76';
            }
        };

        return (
            <div className="header">
                <h1>
                    <div className="logo"><img src="/img/logo_xpaex.png" alt="xpa" /></div>
                    XPA<span className="small-title">Exchange</span>
                </h1>
                <div className="empty" />
                <nav>
                    <div>
                        <Link to="exchange">
                            {t('exchange')}
                        </Link>
                    </div>
                    <div>
                        <a href={guide()} target="_blank">{t('help')}</a>
                    </div>
                    <div>
                        <a onClick={this.scrollTo}>{t('contact')}</a>
                    </div>
                </nav>
                <Language changeHeader={this.changeHeader} />
            </div>
        );
    }
}

Header.defaultProps = {
    t: () => {}
};

Header.propTypes = {
    t: PropTypes.func
};

export default Header;
