import React from 'react';
import PropTypes from 'prop-types';
import { translate } from 'react-i18next';

import './feature.scss';
import update from 'immutability-helper';

@translate('landing', { wait: true })
class Feature extends React.Component
{
    constructor(props)
    {
        super(props);
        const { t } = props;
        this.state = {
            data: [
                {
                    id: 1,
                    img: 'feature-01@2x.png',
                    title: t('feature1-title'),
                    txt: t('feature1')
                },
                {
                    id: 2,
                    img: 'feature-02@2x.png',
                    title: t('feature2-title'),
                    txt: t('feature2')
                },
                {
                    id: 3,
                    img: 'feature-03@2x.png',
                    title: t('feature3-title'),
                    txt: t('feature3')
                },
                {
                    id: 4,
                    img: 'feature-04@2x.png',
                    title: t('feature4-title'),
                    txt: t('feature4')
                },
                {
                    id: 5,
                    img: 'feature-05@2x.png',
                    title: t('feature5-title'),
                    txt: t('feature5')
                },
            ]
        };
    }

    componentWillReceiveProps()
    {
        const { t } = this.props;
        this.setState(update(this.state, {
            data: { $set: [
                {
                    id: 1,
                    img: 'feature-01@2x.png',
                    title: t('feature1-title'),
                    txt: t('feature1')
                },
                {
                    id: 2,
                    img: 'feature-02@2x.png',
                    title: t('feature2-title'),
                    txt: t('feature2')
                },
                {
                    id: 3,
                    img: 'feature-03@2x.png',
                    title: t('feature3-title'),
                    txt: t('feature3')
                },
                {
                    id: 4,
                    img: 'feature-04@2x.png',
                    title: t('feature4-title'),
                    txt: t('feature4')
                },
                {
                    id: 5,
                    img: 'feature-05@2x.png',
                    title: t('feature5-title'),
                    txt: t('feature5')
                },
            ] }
        }));
    }

    renderItem()
    {
        let items = this.state.data.map((data) =>
        {
            return (
                <div className="item" key={data.id}>
                    <div className="img">
                        <img src={`/img/${data.img}`} alt="" />
                    </div>
                    <div className="title">{data.title}</div>
                    <div className="txt">{data.txt}</div>
                </div>
            );
        });

        return items;
    }

    render()
    {
        return (
            <div className="feature">
                {this.renderItem()}
            </div>
        );
    }
}

Feature.defaultProps = {
    t: () => {},
};

Feature.propTypes = {
    t: PropTypes.func
};

export default Feature;
