/* eslint-disable no-undef */
import * as types from '../constants/actionTypesNews';

const news = finalConfig.news;
export function getNews()
{
    return async (dispatch) =>
    {
        let res = await fetch(`https://newsapi.org/v2/everything?sources=${finalConfig.news.source}&apiKey=${news.apiKey}&q=${news.query}`);
        dispatch({
            type: types.SET_NEWS,
            data: res.body.articles
        });
    };
}
